@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body,input,button,textarea{font:600 18px Roboto, sans-serif}a{text-decoration:none}#app{background-color:#ddd;display:flex;flex-direction:column;align-items:center;justify-content:center;min-height:calc(100vh + 10rem)}#app main{flex:1 1;display:flex}#app main section{height:90vh;margin:0 auto !important;border-radius:5px;background:#eee;margin:auto;width:800px;box-shadow:10px 10px 1em gray}@media (max-width: 700px){#app main section{overflow:scroll;width:400px}}.anime-left{transform:translateX(-20px);opacity:0;animation:animeLeft 0.3s forwards}@keyframes animeLeft{to{transform:none;transform:initial;opacity:1;opacity:initial}}@keyframes loading{to{transform:rotate(360deg)}}

nav{margin:1rem}nav ul{display:flex;list-style:none;align-items:center}nav ul li{margin-right:2rem}nav ul li a{display:block;font-size:1.2rem;color:black;background:#7c63bb;color:white;padding:0.5rem 1rem;border-radius:5px;transition:0.5s ease}nav ul li a.active,nav ul li a:hover{transform:scale(1.1);background:#633bc7}

footer{display:flex;align-items:center;justify-content:center;background-color:#633bc7;color:white;height:10rem;width:100%;box-sizing:border-box}

#loading{width:100%;height:100%;display:flex;align-items:center;justify-content:center}#loading div{width:40px;height:40px;border-radius:50%;border:10px solid #4a377c;border-right-color:transparent;animation:loading 1s infinite;margin:10px}

#products{display:flex;align-items:center;justify-content:center}#products #products-list{padding:20px;display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:2rem;gap:2rem}@media (max-width: 700px){#products #products-list{grid-template-columns:1fr;grid-gap:2rem;gap:2rem}}#products #products-list a{display:flex;align-items:center;justify-content:center;color:#633bc7;transition:0.5s ease}#products #products-list a img{height:200px;width:150px;border:1px solid gray;border-radius:5px;transition:0.5s ease}#products #products-list a:hover{transform:scale(1.1)}#products #products-list a:hover img{box-shadow:10px 10px 1rem gray}

#contact{padding:40px;display:grid;grid-template-columns:1fr 1fr;grid-gap:2rem;gap:2rem}#contact img{height:400px;width:300px;border-radius:5px}#contact ul{padding:0px;list-style:none}#contact ul li{margin-bottom:0.8rem}#contact ul li::before{content:'';display:inline-block;width:20px;height:2px;margin-right:10px}

#product-details{padding:20px;display:flex;flex-direction:column;align-items:center}#product-details #content{height:90%}#product-details #content #images-list{display:flex;flex-wrap:wrap;align-items:center;justify-content:space-around;margin-bottom:2rem}#product-details #content #images-list img{border-radius:10px;border:1px solid gray;height:250px;width:200px}#product-details #content #product-infos span,#product-details #content #product-infos h1,#product-details #content #product-infos p{margin:30px}#product-details #content #product-infos h1{color:#4a377c}#product-details #content #product-infos span{padding:0.5rem;background:#633bc7;color:white;border-radius:5px}#product-details #content #product-infos p{font-size:1.2rem;line-height:1.2}#product-details #buttons{display:flex;align-items:center;justify-content:center}#product-details #buttons a{text-align:center;width:100px;background-color:#633bc7;color:white;padding:10px;border-radius:5px;transition:0.5s ease}#product-details #buttons a:hover{background-color:#4a377c}

